/* Основные стили для приложения */
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #6a11cb, #2575fc); /* Градиентный фон */
  font-family: 'Arial', sans-serif;
}

/* Стили для калькулятора */
.calculator {
  background: rgba(255, 255, 255, 0.1); /* Полупрозрачный фон */
  backdrop-filter: blur(10px); /* Размытие фона */
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2); /* Тень */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Светлая рамка */
}

/* Заголовок калькулятора */
.calculator h1 {
  text-align: center;
  color: #fff;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Дисплей калькулятора */
.display {
  background: rgba(0, 0, 0, 0.2); /* Темный полупрозрачный фон */
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 20px;
  text-align: right;
}

.display input {
  width: 100%;
  background: none;
  border: none;
  color: #fff;
  font-size: 28px;
  text-align: right;
  outline: none;
}

.display .result {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.7); /* Полупрозрачный текст */
  margin-top: 5px;
}

/* Кнопки калькулятора */
.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.buttons button {
  padding: 20px;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1); /* Полупрозрачный фон */
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.buttons button:hover {
  background: rgba(255, 255, 255, 0.2); /* Светлее при наведении */
  transform: scale(1.05); /* Увеличение кнопки */
}

.buttons button:active {
  background: rgba(255, 255, 255, 0.3); /* Ещё светлее при нажатии */
  transform: scale(0.95); /* Уменьшение кнопки */
}

/* Специальные стили для кнопок операций */
.buttons button.operator {
  background: #ff9500; /* Оранжевый для операций */
  color: #fff;
}

.buttons button.operator:hover {
  background: #ffaa33; /* Светлее при наведении */
}

.buttons button.operator:active {
  background: #cc7a00; /* Темнее при нажатии */
}

/* Стили для кнопки "C" (очистка) */
.buttons button.clear {
  background: #ff3b30; /* Красный для очистки */
  color: #fff;
}

.buttons button.clear:hover {
  background: #ff5c52; /* Светлее при наведении */
}

.buttons button.clear:active {
  background: #cc2c24; /* Темнее при нажатии */
}

/* Стили для кнопки "=" */
.buttons button.equals {
  background: #34c759; /* Зелёный для "=" */
  color: #fff;
  grid-column: span 2; /* Занимает две колонки */
}

.buttons button.equals:hover {
  background: #5cdb7e; /* Светлее при наведении */
}

.buttons button.equals:active {
  background: #2a9d4a; /* Темнее при нажатии */
}